*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Mulish', sans-serif;
}

.nav-list{
    height: 10vh;
    display: flex;
    width: 50%;
    justify-content: space-around;
    align-items: center;
    margin-left: auto;
    list-style: none;;
}

.nav-link {
    text-decoration: none;
    color: #000;
    font-size: 18px;
}

.home, .about {
    height: 90vh;
    position: absolute;
    width: 100%;
}

.home-content, .about-content{
    height: 100%;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    img{
        height: 75%;
    }
    h1{
        font-size: 50px;
        margin: 30px 0px;
    }
    p{
        font-size: 20px;
    }

}


.home {
    background-image: linear-gradient(to top, #fbc2eb 0%, #a6c1ee 100%);
}

.about {
    background-image: linear-gradient(to top, #cfd9df 0%, #e2ebf0 100%);
}